import React, { Component } from 'react';
import content from '../content'
import 'react-lazy-load-image-component/src/effects/blur.css';


import {LazyLoadImage} from 'react-lazy-load-image-component'
class Stack extends Component {
    render() {
        return (
            <div className="min-h-screen flex flex-col items-center justify-center font-dosis">
                    <h1 className='text-5xl font-bold'>
                        Stack I use
                    </h1>
                    <div className='flex flex-wrap justify-center items-center mt-10'>
                    {content.stack.tech.map((tech, index)=>{
                                return (
                                <span className='w-40 h-40 bg-white shadow-2xl m-2 rounded-full flex items-center justify-center p-5 animate-bounce'>
                                    <LazyLoadImage 
                                        src={tech.img}
                                        alt={tech.alt}
                                        effect="blur" 
                                    />
                                </span>);
                            })}
                    </div>
                    <p className='w-11/12 md:max-w-xl text-xl mx-auto text-center mt-10 '>
                    {content.stack.desc}
                </p>
            </div>

        );
    }
}

export default Stack;
